
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      




































































































.payment {
  background-color: $color-primary;
  background-image: linear-gradient(36deg, #c42037 0%, $color-primary 100%);
  color: white;
  border-radius: 10px;
  padding: 2em;
  box-shadow: 0 0 60px rgba($color-primary, 0.6);

  @include respond-to(small) {
    padding: 3em 4em;
  }
}
.payment-layout {
  @include respond-to(large) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .payment-info {
      margin-bottom: 0;
    }
  }
}
.payment-info {
  margin-bottom: 1em;
}
.payment-action {
  margin-bottom: 0.2em;
  font-size: 20px;
  @include respond-to(small) {
    font-size: 30px;
  }
}
.payment-descriptions {
  font-size: 20px;
}
.payment-price {
  font-size: 20px;
}
.payment-price-value {
  font-weight: bold;
}
.btn-payment {
  box-shadow: 0 8px 46px rgba(0, 0, 0, 0.46);
  background: white;
  color: $color-primary;
  border-radius: 10px;
  padding: 0.6em 1em;
  font-size: 26px;

  &:hover {
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.46);
  }
}
.payment-message {
  font-size: 20px;
  text-align: right;
  margin-left: 1em;
}
.payment-message-error {
  margin-bottom: 1em;
}
