
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      













































































































































































.upme-menu {
  position: fixed;
  z-index: 12;
  top: 0;
  left: 0;
  width: 280px;
  height: 100%;
  height: 100vh;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.2s ease-in-out;
  background-color: white;
  padding-bottom: 5em;

  &.active {
    transform: translate3d(0, 0, 0);
  }

  @include respond-to(medium) {
    transform: none;
    position: static;
    padding-bottom: 0;
  }
}
.upme-menu-overlay {
  position: fixed;
  z-index: 11;
  right: 0;
  width: 100%;
  height: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);

  @include respond-to(medium) {
    display: none;
  }
}
.upme-menu-top {
  padding: 25px 0;
}
.upme-menu-links-group {
  &:not(:last-child) {
    border-bottom: 1px solid #d5d5d6;
  }
}
.upme-menu-link {
  padding: 1em;
  display: flex;
  align-items: center;
  .img-container {
    margin-right: 2em;
    img {
      width: 16px;
    }
  }
  &:hover,
  &.router-link-active {
    opacity: 0.6;
  }
}
