
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      


































.user-preview {
  display: none;
  @include respond-to(medium) {
    display: block;
  }
}
.user-preview-layout {
  display: flex;
  align-items: center;
}
.user-preview-name {
  font-size: 11px;
  max-width: 200px;
  overflow: hidden;
  margin-left: 1em;
}
.user-preview-link {
  &:hover {
    opacity: 0.6;
  }
}
