.page-enter-active,
.page-leave-active {
  transition: opacity 0.5s ease; }

.page-enter,
.page-leave-active {
  opacity: 0; }

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease; }

.fade-enter,
.fade-leave-active {
  opacity: 0; }

.slide-down-enter-active,
.slide-down-leave-active {
  transition: all 1s ease;
  overflow: hidden; }

.slide-down-enter-active {
  max-height: 200px; }

.slide-down-enter,
.slide-down-leave-active {
  max-height: 0; }

.slide-down-leave {
  max-height: 200px; }

.appear-enter-active,
.appear-leave-active {
  transition: all 0.3s ease; }

.appear-enter,
.appear-leave-active {
  opacity: 0;
  transform: translate3d(0, 1em, 0); }

.check-inst-enter-active,
.check-inst-leave-active {
  transition: all 3s ease;
  overflow: hidden; }

.check-inst-enter-active {
  max-height: 1000px;
  opacity: 1; }

.check-inst-enter,
.check-inst-leave-active {
  max-height: 0;
  opacity: 0; }

.check-inst-leave {
  max-height: 1000px;
  opacity: 1; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes slideUp {
  from {
    transform: translate3d(0, 2em, 0); }
  to {
    transform: translate3d(0, 0, 0); } }

@keyframes bouncing {
  from {
    transform: translate3d(0, 10px, 0) scale(1.2, 0.85); }
  to {
    transform: translate3d(0, -20px, 0) scale(0.9, 1.1); } }

@keyframes patrolling {
  from {
    left: 17px; }
  50% {
    left: 100%;
    left: calc(100% - 17px); }
  to {
    left: 17px; } }

@keyframes check-bounce {
  from {
    transform: translate3d(50%, 0, 0) scaleX(2) rotate(90deg); }
  50% {
    transform: translate3d(50%, 10%, 0) scaleX(2) rotate(90deg); }
  to {
    transform: translate3d(50%, 0, 0) scaleX(2) rotate(90deg); } }

@keyframes rotate {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(360deg); } }

.btn {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  cursor: pointer;
  background: #c42037 linear-gradient(27deg, #c42037 0%, #ff030f 100%);
  color: white;
  border-radius: 2em;
  padding: 0.2em 1em;
  box-shadow: 0 0 1em rgba(255, 3, 15, 0.8);
  transition: all 0.2s ease-in-out; }
  .btn:hover {
    box-shadow: 1px 1px 1.6em rgba(255, 3, 15, 0.8); }
  .btn:focus {
    box-shadow: 0 0 0.6em rgba(255, 3, 15, 0.8); }
  .btn:disabled {
    opacity: 0.6;
    cursor: default; }

