
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      





















































































.task {
  position: relative;
  padding: 1em 2em;
  border-radius: 20px;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.2);
  margin-bottom: 2em;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &:last-child {
    margin-bottom: 0;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 12px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    background: linear-gradient(to top, #c42037 0%, $color-primary 100%);
  }

  &.verifying {
    .btn {
      background: #ffe110 linear-gradient(27deg, #f3a948 0%, #ffe110 100%);
      box-shadow: 0 0 1em rgba(#ffe110, 0.4);
    }
    &::before {
      background-image: linear-gradient(to top, #f3a948 0%, #ffe110 100%);
    }
  }

  &.completed {
    .btn {
      background: #15ff73 linear-gradient(27deg, #15ff73 0%, #fff715 100%);
      box-shadow: 0 0 1em rgba(#15ff73, 0.4);
      color: $color-text;
    }
    &::before {
      background-image: linear-gradient(to top, #15ff73 0%, #fff715 100%);
    }
  }

  .btn {
    padding: 0.2em 1em;
    margin-right: 0.4em;
    @include respond-to(x-small) {
      padding: 0.2em 2em;
    }
  }
}
.task-title {
  margin-bottom: 0.4em;
}
.task-description {
  margin-bottom: 1em;
}
.task-layout {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.task-reward {
  padding: 0.2em 1em;
  border-radius: 15px;
  background-image: linear-gradient(27deg, #15ff73 0%, #fff715 100%);
}
.task-url {
  margin-bottom: 1em;
}
.task-form {
  background: transparent;
  color: inherit;
  padding: 0;

  .btn {
    color: white;
  }
}
