
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      





















.user-avatar {
  display: inline-block;
  position: relative;
  width: 55px;
  height: 55px;
  &::before {
    content: "";
    position: absolute;
    top: -5px;
    left: -5px;
    bottom: -5px;
    right: -5px;
    border: 2px solid $color-primary;
    border-radius: 50%;
  }
  &.profile-info-avatar {
    width: 109px;
    height: 109px;
  }
}
.user-avatar-image {
  border-radius: 50%;
}
