
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      























































































































// TODO: mobile + perfect scss
.photographer-nav {
  margin-bottom: 2em;
}
/deep/ .VueCarousel-dot {
  margin-top: 0 !important;
}
.photographer-carousel-slide {
  padding: 2em;
}
.photographer {
  display: block;
  padding: 1em;
  background-color: $color-primary;
  background-image: linear-gradient(36deg, #c42037 0%, $color-primary 100%);
  color: white;
  border-radius: 10px;
  box-shadow: 0 0 3em $color-primary;

  @include respond-to(small) {
    padding: 2em;
  }
}
.photographer-layout {
  text-align: center;
  @include respond-to(x-small) {
    text-align: left;
    display: flex;
    align-items: center;

    .photographer-avatar {
      margin: 0 2em 0 0;
      flex-shrink: 0;
    }
  }
}
.photographer-avatar {
  width: 150px;
  height: 150px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 10px;
  margin: 0 auto 1em;
}
.photographer-title {
  // margin-bottom: 1em;
  @include respond-to(small) {
    font-size: 1.6em;
  }
}
.photographer-nickname {
  margin-bottom: 1em;
}
.photographer-nickname-link {
  font-weight: bold;

  &:hover {
    opacity: 0.7;
  }
}
.photographer-link {
  font-size: 1.6em;
  padding: 0.4em 2em;
  background: white;
  color: $color-primary;
  box-shadow: 0 0 1em white;

  &:hover {
    box-shadow: 0 0 0.4em white;
  }
}
.content-photo-layout {
  padding-top: 1.4em;
  border-top: 1px solid rgba(grey, 0.5);

  .content-photo-grid {
    margin-bottom: 1.4em;
  }

  @include respond-to(small) {
    display: flex;
    justify-content: space-between;

    .content-photo-grid {
      flex-grow: 1;
      margin-right: 1.4em;
      margin-bottom: 0;
      align-self: flex-start;
    }

    .content-photo-advantages {
      width: 50%;
    }
  }

}
.content-photo-advantages {
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.4);
  border-radius: 10px;
}
.content-photo-advantages-top {
  padding: 2em;
  padding-left: 5em;
  background-color: $color-primary;
  background-image: linear-gradient(36deg, #c42037 0%, $color-primary 100%);
  color: white;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.content-photo-advantages-bottom {
  padding: 2em;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.content-photo-advantages-title {
  font-size: inherit;
}
.content-photo-advantage {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  &::before {
    content: "";
    display: inline-block;
    width: 2em;
    height: 2em;
    border-radius: 50%;
    background-color: $color-primary;
    background-image: linear-gradient(36deg, #c42037 0%, $color-primary 100%);
    flex-shrink: 0;
    margin-right: 1em;
  }
  &:not(:last-child) {
    margin-bottom: 1em;
  }
}
