
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      




































































































































.comments {
  margin-bottom: 2em;
  @include respond-to(small) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;

    .comment {
      width: 48%;
      width: calc(50% - 1em);
      &:nth-last-child(2) {
        margin-bottom: 0;
      }
    }
  }
}
.comments-need {
  margin-bottom: 1em;
}
.comment-message {
  margin-bottom: 1em;
}
.success-comment {
  max-width: 469px;
  margin: 0 auto;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.2);
  border-radius: 15px;
}
.success-comment-top {
  text-align: center;
  background-image: linear-gradient(66deg, #15ff73 0%, #fff715 100%);
  border-radius: 15px;
  padding: 2em;
}
.success-comment-title {
  margin-top: 0.2em;
  margin-bottom: 0.2em;
  font-weight: 700;
  font-size: 21px;
}
.success-comment-form {
  padding: 2em 1em;

  input,
  textarea {
    margin-bottom: 0.6em;
    background-color: transparent;
    color: inherit;
    padding: 0.3em;
    transition: all 0.3s;
    border: 1px solid #7d76a0;
    border-radius: 10px;
    box-shadow: none;
  }

  label {
    padding-left: 10px;
    margin-bottom: 0.4em;
    font-size: 14px;
  }
}
.success-comment-hint {
  color: #c6c6c6;
}
.success-comment-submit {
  display: block;
  margin-left: auto;
}
