.wheel-me-indicator {
  position: absolute;
  top: -6px;
  left: 50%;
  transform: translate3d(-50%, -100%, 0);
  @include respond-to(small, max) {
    width: 25px;
  }
}
.wheel-me {
  position: relative;
  display: inline-block;
  max-width: 100%;
  .wheel-me-action {
    position: absolute;
    left: 33%;
    top: 33%;
    width: 34%;
    height: 34%;
    padding: 0;
    border-radius: 50%;
    background-image: linear-gradient(42deg, #bbbbbb 0%, #ffffff 100%);
    color: $color-primary;
    box-shadow: 0 0 1.2em rgba(0, 0, 0, 0.8);
    transition: opacity 0.2s ease-in-out;
    &:hover {
      opacity: 0.7;
    }
  }
}
.wheel-me-img {
  transition: transform 10s cubic-bezier(.84,.05,.41,.94);

  svg {
    width: 479px;
    max-width: 100%;
  }

  &.rotate-6 {
    transform: rotate(1440deg);
  }
  &.rotate-1 {
    transform: rotate(1395deg);
  }
  &.rotate-7 {
    transform: rotate(1350deg);
  }
  &.rotate-2 {
    transform: rotate(1305deg);
  }
  &.rotate-8 {
    transform: rotate(1260deg);
  }
  &.rotate-3 {
    transform: rotate(1215deg);
  }
  &.rotate-5 {
    transform: rotate(1170deg);
  }
  &.rotate-4 {
    transform: rotate(1125deg);
  }
}
.st0{fill:url(#SVGID_1_);}
.st1{fill:#470612;}
.st2{fill:url(#SVGID_2_);}
.st3{fill:url(#SVGID_3_);}
.st4{fill:url(#SVGID_4_);}
.st5{fill:#FFFFFF;fill-opacity:0.2;}
.st6{fill:#FFFFFF;}
.cls-1 {
  fill-rule: evenodd;
  fill: url(#linear-gradient);
}
