
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      

















































.giveaway-header-layout {
  display: flex;
  align-items: center;
}
.giveaway-chat-btn {
  font-size: 16px;
  margin-left: 1em;
  @include respond-to(x-small) {
    font-size: 23px;
  }
}
.giveaway-list {
  .giveaway-item {
    margin-bottom: 2em;
    &:last-child {
      margin-bottom: 0;
    }
  }
  @include respond-to(small) {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

    .giveaway-item {
      max-width: 45%;
      max-width: calc(50% - 1em);

      &:nth-child(2n-1) {
        margin-right: 2em;
      }
      &:nth-last-child(2) {
        margin-bottom: 0;
      }
    }
  }
}
