
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      







































































































































































































































































































.home-registration {
  padding: 4em 0;
  @include respond-to(large) {
    padding: 12em 0;
  }
}
.decor-item-1 {
  bottom: 10%;
  right: -10%;
  width: 326px;
  height: 326px;
  border-radius: 56px;
  background-image: linear-gradient((52deg-45deg), #c42037 0%, #ff030f 100%);
  box-shadow: -7em 7em 16em 0.4em rgba($color-primary, 0.5);
}
.decor-item-2 {
  top: -30%;
  right: -15%;
  width: 500px;
  height: 500px;
  background-image: linear-gradient(45deg, #f3c111 0%, #ee0727 100%);
  box-shadow: 0 2em 6em 0.4em rgba(#f3c111, 0.8);
  @include respond-to(x-large) {
    top: -40%;
    width: 694px;
    height: 694px;
  }
}
.decor-item-3 {
  top: 10%;
  left: -2%;
  width: 280px;
  height: 280px;
  border-radius: 56px;
  background-image: linear-gradient((52deg-45deg), #c42037 0%, #ff030f 100%);
  box-shadow:
    4em -6em 12em 1em rgba(#f3c111, 0.5),
    4em 6em 12em 1em rgba($color-primary, 0.5);
  @include respond-to(x-large) {
    width: 326px;
    height: 326px;
  }
}
