
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      






















































































































































.premium-buy {
  background-color: $color-primary;
  background-image: linear-gradient(20deg, #c42037 0%, #ff030f 100%);
  color: white;
  padding: 2em;
  text-align: center;
  border-radius: 10px;
}
.premium-buy-title {
  font-size: 29px;
  margin-bottom: 0.6em;
}
.premium-buy-description-carousel {
  max-width: 595px;
  margin: 0 auto 1em;

  /deep/ .VueCarousel-navigation-button {
    height: 40px;
    width: 40px;
  }
  /deep/ .VueCarousel-navigation-button::before {
    content: "";
    position: absolute;
    top: 0;
    height: 40px;
    width: 40px;
    background-size: cover;
    background-repeat: no-repeat;
  }

  /deep/ .VueCarousel-navigation-next::before {
    background-image: url('../../assets/icons/next.svg');
    right: 6px;
  }

  /deep/ .VueCarousel-navigation-prev::before {
    background-image: url('../../assets/icons/prev.svg');
    left: 6px;
  }
}
.premium-buy-description {
  padding: 0 2em;
  .img-container {
    margin-bottom: 1em;
  }
  @include respond-to(x-small) {
    font-size: 21px;
  }
  @include respond-to(small) {
    display: flex;
    align-items: center;
    justify-content: center;
    .img-container {
      margin-bottom: 0;
    }
  }
}
.premium-buy-description-text {
  margin-left: 1em;
}
.premium-form-layout {
  @include respond-to(small) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .form-block-radio {
      width: 45%;
      width: calc(50% - 0.5em);
      &:nth-last-child(1),
      &:nth-last-child(2) {
        margin-bottom: 0;
      }
    }
  }
  @include respond-to(large) {
    .form-block-radio {
      width: 25%;
      width: calc(25% - 4em / 3);
      margin-bottom: 0;
    }
  }
}
.premium-title {
  color: $color-primary;
  font-size: 24px;
  margin-bottom: 1em;
}
.premium-price {
  font-weight: bold;
  font-size: 33px;
  margin-bottom: 0.1em;
}
.premium-price-prev {
  font-size: 24px;
  text-decoration-color: $color-primary;
}
