        @import "@/assets/scss/utils/_mixins.scss";
      
























.chart-carousel {
  position: static;
  &.VueCarousel {
    position: static;
  }
  .VueCarousel-navigation {
    font-size: 28px;

    @include respond-to(x-small, max) {
      .VueCarousel-navigation-button {
        color: white;
      }
      .VueCarousel-navigation-prev {
        left: 10%;
      }
      .VueCarousel-navigation-next {
        right: 10%;
      }
    }
  }
  .VueCarousel-slide {
    max-width: 100%;
  }
}
