
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      










































































.rating {
  counter-reset: rating;
}
.rating-layout {
  @include respond-to(small) {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;

    .rating-lists {
      width: 70%;
    }

    .rating-prizes {
      width: 25%;
      padding: 2.7em 0;
    }
  }
}
.rating-prizes {
  margin-bottom: 2em;
  @include respond-to(small) {
    margin-bottom: 0;
  }
}
.rating-prizes-list {
  display: flex;
  justify-content: space-between;

  .rating-prizes-list-item {
    flex-grow: 1;
    &:not(:last-child) {
      margin-right: 1em;
    }
  }

  font-size: 10px;

  @include respond-to(x-small) {
    font-size: 16px;
  }

  @include respond-to(small) {
    font-size: 19px;
    flex-direction: column;
    .rating-prizes-list-item {
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 2.4em;
      }
    }
  }
}
.rating-prizes-list-laureates {
  margin-bottom: 1em;
  @include respond-to(small) {
    margin-bottom: 2.4em;
  }
  .rating-prizes-list-item {
    &:nth-child(1) {
      background-color: gold;
      background-image: linear-gradient(29deg, #ceae56 0%, #fdeea9 50%, #c7a84d 100%);
    }

    &:nth-child(2) {
      background-color: silver;
      background-image: linear-gradient(29deg, #898989 0%, #fcfcfc 29%, #949494 64%, #f5f5f5 100%);
    }

    &:nth-child(3) {
      background-color: brown;
      background-image: linear-gradient(29deg, #75432c 0%, #bd8556 28%, #ebbd85 45%, #d2a876 61%, #ae7645 81%, #75422d 100%);
    }
  }
}
.rating-prizes-list-item {
  border-radius: 10px;
  background-color: $color-primary;
  background-image: linear-gradient(36deg, #c42037 0%, $color-primary 100%);
  color: white;
  text-align: center;
  padding: 0.6em 1em;
  font-weight: 700;
}
.rating-lists-bottom {
  padding: 0 1em;
}
