
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      

































































.contacts-list {
  font-size: $font-size-xxx-large;
  @include respond-to(medium) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .contacts-list-item {
      width: 48%;
      width: calc(50% - 0.5em);
      &:nth-last-child(2) {
        margin-bottom: 0;
      }
    }
    .img-container {
      flex-shrink: 0;
    }
  }
}
.contacts-list-item {
  margin-bottom: 1em;
  &:last-child {
    margin-bottom: 0;
    .contacts-list-item-title {
      font-size: 20px;
      font-weight: 700;
      @include respond-to(x-small) {
        font-size: 27px;
      }
    }
    .contacts-list-link {
      padding: 1em 19%;
    }
    .img-container {
      margin-right: 1em;
      flex-shrink: 0.2;
    }
  }
}
.contacts-list-link {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  color: white;
  height: 200px;
  background-image: linear-gradient(38deg, #c42037 0%, $color-primary 100%);
  box-shadow: 0 0 0.6em rgba(0, 0, 0, 0.3);

  &:hover {
    opacity: 0.6;
  }
}
.contacts-list-item-title {
  font-size: 0;
}
