
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      























































































































































































.profile-edit-form {
  font-size: 18px;
  @include respond-to(small) {
    font-size: 23px;
  }
  position: relative;
}
.profile-edit-cancel {
  position: absolute;
  top: 1em;
  right: 1em;
  width: 33px;
  height: 33px;
  @include respond-to(small) {
    top: 2em;
    right: 2em;
  }
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    width: 100%;
    height: 2px;
    display: block;
    background-color: white;
    transition: transform 0.3s ease-in-out;
  }
  &::before {
    transform: rotate(45deg);
  }
  &::after {
    transform: rotate(-45deg);
  }
  &:hover {
    &::before {
      transform: rotate(180deg);
    }
    &::after {
      transform: rotate(0);
    }
  }
}
.profile-edit-layout {
  margin-bottom: 1em;

  @include respond-to(small) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .form-block {
      width: 48%;
      width: calc(50% - 0.5em);

      &:nth-last-child(2) {
        margin-bottom: 0;
      }
    }
  }
}
.profile-edit-form-bottom {
  text-align: right;
}
