.privacy-policy-page {
  padding: 2em 0;
}
.privacy-policy-top {
  text-align: right;
  font-weight: bold;
  margin-bottom: 1em;
}
h1 {
  text-align: center;
  font-size: 24px;
  margin-bottom: 1em;
}
ol {
  counter-reset: section;
  list-style-type: none;
