
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      












































































































.comment {
  padding-left: 12px;
  position: relative;
  border-radius: 20px;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.2);
  margin-bottom: 2em;
  text-align: right;
  &:last-child {
    margin-bottom: 0;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 12px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    background: linear-gradient(to top, #c42037 0%, $color-primary 100%);
  }

  &.verifying {
    .comment-url {
      background: #ffe110 linear-gradient(27deg, #f3a948 0%, #ffe110 100%);
      box-shadow: 0 0 1em rgba(#ffe110, 0.4);
    }
    &::before {
      background-image: linear-gradient(to top, #f3a948 0%, #ffe110 100%);
    }
  }

  &.completed {
    .comment-url {
      background: #15ff73 linear-gradient(27deg, #15ff73 0%, #fff715 100%);
      box-shadow: 0 0 1em rgba(#15ff73, 0.4);
      color: $color-text;
    }
    &::before {
      background-image: linear-gradient(to top, #15ff73 0%, #fff715 100%);
    }
  }
  .btn {
    padding: 0.2em 2em;
    width: 100%;
  }
}
.comment-info-text {
  margin-bottom: 1em;
}
.comment-message {
  margin-top: 1em;
}
.comment-actions {
  @include respond-to(x-small) {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    .btn {
      width: auto;
    }
    .comment-check {
      margin-bottom: 0;
      margin-left: 1em;
    }
  }
  @include respond-to(small) {
    flex-wrap: wrap;
    .btn {
      width: 100%;
    }
    .comment-check {
      margin-right: 0;
      margin-bottom: 0.6em;
    }
  }
  @include respond-to(large) {
    .btn {
      width: auto;
    }
    .comment-check {
      margin-right: 1em;
      margin-bottom: 0;
    }
  }
}
.comment-check {
  margin-bottom: 0.6em;
  background: #ffe110 linear-gradient(27deg, #f3a948 0%, #ffe110 100%);
  box-shadow: 0 0 2em #ffe110;

  &:hover,
  &:focus {
    box-shadow: 0 0 0.4em #ffe110;
  }
}
.comment-info {
  padding: 1em;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.comment-description {
  text-align: left;
}
.comment-layout {
  @include respond-to(x-small) {
    display: flex;
    justify-content: space-between;

    .comment-img {
      flex-shrink: 0;
      width: 107px;
      height: 107px;
    }
  }
}
.comment-img {
  margin: 0 auto;
  width: 100%;
  height: 300px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
