
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      





















































.video-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  @include respond-to(xs-small) {
    justify-content: flex-start;
    .video-list-item {
      width: 45%;
      width: calc(50% - 1em);
      margin-right: 2em;
      &:nth-child(2n) {
        margin-right: 0;
      }
    }
  }
  @include respond-to(small) {
    .video-list-item {
      width: 30%;
      width: calc(100% / 3 - 4em / 3);
      &:nth-child(2n) {
        margin-right: 2em;
      }
      &:nth-child(3n) {
        margin-right: 0;
      }
    }
  }
  @include respond-to(large) {
    .video-list-item {
      width: 20%;
      width: calc(25% - 1.5em);
      &:nth-child(3n) {
        margin-right: 2em;
      }
      &:nth-child(4n) {
        margin-right: 0;
      }
    }
  }
}
.video-list-item {
  margin-bottom: 2em;
  &:nth-child() {
    margin-bottom: 0;
  }
}
.video-list-item-title {
  position: relative;
  font-size: 23px;
  font-weight: 400;
  color: #c81e34;
  margin-bottom: 1em;

  &::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    display: block;
    width: 253px;
    max-width: 100%;
    height: 4px;
    background-color: #c81e34;
    background-image: linear-gradient(to right, #c42037 0%, #ff030f 100%);
  }
}
