
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      




















































































.wheel-me-header {
  margin-bottom: 6em;
  position: relative;
  @include respond-to(small) {
    margin-bottom: 5em;
  }
}
.wheel-me-header-layout {
  display: flex;
  align-items: center;
}
.headline-tip-content {
  position: absolute;
  bottom: -1em;
  transform: translate3d(0, 100%, 0);
  padding: 0.6em 1em;
  box-shadow: 0 10px 2em rgba(0, 0, 0, 0.3);
  border-radius: 2em;
  @include respond-to(large) {
    bottom: 50%;
    transform: translate3d(0, 50%, 0);
    left: 240px;
  }
}
.headline-tip-question {
  color: $color-primary;
  border-radius: 50%;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.3);
  background: white;
  width: 50px;
  height: 50px;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  transition: box-shadow 0.3s;
  margin-left: 1rem;
  cursor: pointer;
  &:focus,
  &:hover {
    box-shadow: 0 0 0.4em rgba(0, 0, 0, 0.3);
  }
}
.wheel-me-layout {
  @include respond-to(large) {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
}
.wheel-me-container {
  margin-bottom: 2em;
  text-align: center;
  @include respond-to(large) {
    margin-bottom: 0;
  }
}
.wheel-me-stats {
  text-align: center;
  flex-grow: 1;
}
.wheel-me-prize {
  padding: 1em;
  border-radius: 10px;
  background-image: linear-gradient(32deg, #15ff73 0%, #fff715 100%);
  box-shadow: -4px 0 0.6em rgba(21, 255, 115, 0.5);
  margin-bottom: 1em;
  font-size: $font-size-xxx-large;
}
.wheel-me-spins {
  color: $color-primary;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.wheel-me-spins-add {
  border-radius: 50%;
  width: 46px;
  height: 46px;
  padding: 0;
  font-size: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 0.2em;
}
