
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      








































































































































































.giveaway-item {
  border-radius: 10px;
  background-color: $color-primary;
  background-image: linear-gradient(41deg, #c42037 0%, #ff030f 100%);
  color: white;
  padding: 1em;
  @include respond-to(xx-small) {
    padding: 2em;
  }
  @include respond-to(small) {
    width: 400px;
    max-width: 100%;
  }
  @include respond-to(medium) {
    padding: 1em;
  }
  @include respond-to(large) {
    padding: 2em;
  }
}
.giveaway-title {
  text-transform: uppercase;
  font-size: 24px;
  text-align: center;
  margin-bottom: 0.8em;
  @include respond-to(xs-small) {
    font-size: 29px;
  }
  @include respond-to(small) {
    font-size: 24px;
  }
}
.giveaway-images {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1em;

  max-width: 350px;
  margin: 0 auto 1.6em;
}
.giveaway-images-item {
  width: 30%;
  position: relative;

  &:nth-child(1) {
    transform: translate3d(10%, 0, 0);
  }

  &:nth-child(2) {
    z-index: 1;
    transform: scale(1.4);
  }

  &:nth-child(3) {
    transform: translate3d(-10%, 0, 0);
  }
}
.giveaway-image {
  border-radius: 50%;
  box-shadow: -5px 15px 60px rgba(0, 0, 0, 0.49);
}
.giveaway-nickname {
  position: relative;
  z-index: 1;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 0.2em;
  @include respond-to(xs-small) {
    font-size: 29px;
  }
  @include respond-to(small) {
    font-size: 20px;
  }
}
.giveaway-nickname-link {
  &:hover {
    opacity: 0.7;
  }
}
.giveaway-stats-carousel {
  position: relative;
  z-index: 1;

  /deep/ .VueCarousel-navigation-button {
    top: 5%;
    color: white;
    transform: translate3d(0, -50%, 0) scaleY(1.8);
    line-height: 1;
    font: inherit;
    font-size: 30px;
  }
}
.giveaway-stats-title {
  position: relative;
  z-index: 1;
  font-size: 17px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 1.6em;
  @include respond-to(xs-small) {
    font-size: 20px;
  }
  @include respond-to(small) {
    font-size: 17px;
  }
}
.giveaway-stats-list {
  display: flex;
  flex-wrap: wrap;
  @include respond-to(xs-small) {
    padding: 0 8%;
  }
  @include respond-to(small) {
    padding: 0;
  }
}
.giveaway-stats-item {
  width: 50%;
  width: calc(50% - 0.6em);
  display: flex;
  margin-bottom: 0.4em;

  &:nth-last-child(1),
  &:nth-last-child(2) {
    margin-bottom: 0;
  }

  &::before {
    content: "";
    display: inline-block;
    width: 30px;
    height: 37.5px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    flex-shrink: 0;
    margin-right: 0.4em;
    @include respond-to(x-small) {
      width: 40px;
      height: 50px;
      margin-right: 0.6em;
    }
    @include respond-to(medium) {
      width: 30px;
      height: 37.5px;
    }
    @include respond-to(large) {
      width: 40px;
      height: 50px;
    }
  }

  &-followers {
    &::before {
      background-image: url("../../assets/icons/giveaway/subscribers.svg")
    }
  }
  &-likes {
    &::before {
      background-image: url("../../assets/icons/giveaway/likes.svg")
    }
  }
  &-comments {
    &::before {
      background-image: url("../../assets/icons/giveaway/comments.svg")
    }
  }
  &-location {
    &::before {
      background-image: url("../../assets/icons/giveaway/location.svg")
    }
  }
  &-age {
    &::before {
      background-image: url("../../assets/icons/giveaway/age.svg")
    }
  }
  &-sex {
    &::before {
      background-image: url("../../assets/icons/giveaway/sex.svg")
    }
  }
}
.giveaway-stats-item-value {
  font-size: 18px;
  font-weight: 700;
  @include respond-to(xs-small) {
    font-size: 24px;
  }
  @include respond-to(small) {
    font-size: 18px;
  }
}
.giveaway-stats-item-description {
  font-size: 13px;
  @include respond-to(xs-small) {
    font-size: 17px;
  }
  @include respond-to(small) {
    font-size: 13px;
  }
}
.giveaway-stats-bottom {
  text-align: center;
}
.giveaway-stats-toggle {
  position: relative;
  background: transparent;
  transition: all 0.3s;
  padding: 4px;
  width: 28px;
  height: 28px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 12px;
    height: 2px;
    background-color: white;
    top: 46%;
  }

  &::before {
    left: 4px;
    transform: rotate(45deg);
  }

  &::after {
    right: 4px;
    transform: rotate(-45deg);
  }

  &:hover {
    opacity: 0.6;
  }

  &.giveaway-stats-toggle-rotate {
    transform: rotate(180deg);
  }
}
.giveaway-hr {
  width: 100%;
  margin: 1em auto;
  height: 1px;
  background: white;
}
.giveaway-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.6em;
  @include respond-to(xs-small) {
    font-size: 27px;
  }
  @include respond-to(small) {
    font-size: 17px;
  }
}
.giveaway-info-value {
  @include respond-to(xs-small) {
    font-size: 29px;
  }
  @include respond-to(small) {
    font-size: 19px;
  }
}
.giveaway-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.giveaway-price {
  @include respond-to(xs-small) {
    font-size: 29px;
  }
  @include respond-to(small) {
    font-size: 19px;
  }
}
.giveaway-btn-submit {
  border-radius: 1em;
  background-color: white;
  color: $color-primary;
  padding: 0.4em 2em;
  font-size: 16px;
  box-shadow: 0 0 0.6em white;

  @include respond-to(xs-small) {
    font-size: 29px;
  }
  @include respond-to(small) {
    font-size: 17px;
  }

  &:hover {
    opacity: 0.6;
  }
}
.bloger-avatar {
  border-radius: 50%;
  width: 63px;
  height: 63px;
  box-shadow: -2px 2px 20px rgba(0, 0, 0, 0.49);
  @include respond-to(xs-small) {
    width: 90px;
    height: 90px;
  }
  @include respond-to(small) {
    width: 63px;
    height: 63px;
  }
}
.giveaway-stats-blogers {
  max-height: 200px;
  overflow: auto;
  padding: 0.2em 0;
}
.giveaway-stats-bloger {
  margin-bottom: 2em;
  &:last-child {
    margin-bottom: 0;
  }
}
.giveawat-stats-bloger-layout {
  display: flex;
  align-items: center;
  justify-content: center;
}
.bloger-avatar-link {
  margin-right: 1em;
  flex-shrink: 0;

  @include respond-to(x-small) {
    margin-right: 2em;
  }
  @include respond-to(medium) {
    margin-right: 1em;
  }
  @include respond-to(large) {
    margin-right: 2em;
  }

  &:hover {
    opacity: 0.6;
  }
}
.bloger-title {
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 0.2em;
  @include respond-to(x-small) {
    font-size: 18px;
  }
  @include respond-to(xs-small) {
    font-size: 24px;
  }
  @include respond-to(small) {
    font-size: 18px;
  }
  @include respond-to(medium) {
    font-size: 14px;
  }
  @include respond-to(large) {
    font-size: 18px;
  }
}
.giveaway-type {
  @include respond-to(xs-small) {
    font-size: 29px;
  }
  @include respond-to(small) {
    font-size: 17px;
  }
}
