
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      

































.upme-content {
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  height:100%;
  height: 100vh;
  transform: translateZ(0);
  @include respond-to(medium) {
    box-shadow: 0 16px 60px rgba(0, 0, 0, 0.22);
    border-radius: 10px;
  }
}
