
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      

































































































.content-design-title {
  font-size: 21px;
  // margin-bottom: 1em;
  @include respond-to(small) {
    font-size: 29px;
  }
}
.content-design-layout {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  margin-bottom: 2em;

  @include respond-to(large) {
    padding: 0 4em;
  }
}
// .content-design-item {
// }
.content-design-item-image {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: $color-primary;
  background-image: linear-gradient(48deg, #c42037 0%, #ff030f 100%);
  color: white;
  margin: 0 auto 1em;

  @include respond-to(x-small) {
    width: 100px;
    height: 100px;
  }

  @include respond-to(small) {
    width: 130px;
    height: 130px;
  }

  @include respond-to(medium) {
    width: 178px;
    height: 178px;
  }
}
.content-design-item-title {
  font-size: inherit;
  font-weight: normal;

  @include respond-to(small) {
    font-size: 21px;
  }
}
.content-design-item-plus {
  font-size: 51px;
  padding-bottom: 1em;
}
.content-design-bottom {
  background-color: $color-primary;
  background-image: linear-gradient(36deg, #c42037 0%, $color-primary 100%);
  color: white;
  border-radius: 10px;
  margin-bottom: 2em;
  padding: 2em 4em;
  box-shadow: 0 0 60px rgba($color-primary, 0.6);
}
.content-design-bottom-layout {
  text-align: center;
  @include respond-to(small) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;

    .content-design-bottom-title {
      margin-bottom: 0;
    }
  }
}
.content-design-bottom-title {
  font-size: 1.6em;
  margin-bottom: 0.4em;
}
.content-design-link {
  display: inline-block;
  cursor: pointer;
  box-shadow: 0 8px 46px rgba(0, 0, 0, 0.46);
  background-color: white;
  color: $color-primary;
  border-radius: 10px;
  padding: 0.6em 1em;
  font-size: 26px;

  &:hover {
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.46);
  }
}
