
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      































































.likes-post-list {
  @include respond-to(x-small) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .likes-post-list-item {
      width: 48%;
      width: calc(50% - 1em);

      &:nth-last-child(2) {
        margin-bottom: 0;
      }
    }
  }

  @include respond-to(small) {
    justify-content: flex-start;
    .likes-post-list-item {
      width: 30%;
      width: calc(100% / 3 - 4em / 3);
      transition: opacity 0.3s;
      margin-right: 2em;

      &:hover {
        opacity: 0.7;
      }

      &:nth-child(3n) {
        margin-right: 0;
      }

      &:nth-last-child(3) {
        margin-bottom: 0;
      }
    }
  }

  @include respond-to(medium) {
    padding: 0 4em;
  }
}
.likes-post-list-item {
  margin-bottom: 2em;

  &:last-child {
    margin-bottom: 0;
  }
}
.likes-post-meta {
  padding: 0.6em 1em;
  background-color: white;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  color: #c81e34;
  font-weight: 700;
  display: flex;
  justify-content: center;
}
.likes-post-meta-likes {
  margin-right: 1.2em;
  &::before {
    content: "";
    width: 27px;
    height: 23px;
    display: inline-block;
    margin-right: 0.6em;
    background-image: url('../../../../assets/icons/promotion/likes/likes.svg');
  }
}
.likes-post-meta-comments {
  &::before {
    content: "";
    width: 25px;
    height: 25px;
    display: inline-block;
    margin-right: 0.6em;
    background-image: url('../../../../assets/icons/promotion/likes/comments.svg');
  }
}
.likes-post-meta-likes,
.likes-post-meta-comments {
  display: flex;
  align-items: center;
}
.likes-post-img {
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  padding-top: 100%;
  height: 0;
}
.likes-more-container {
  text-align: center;
  margin-top: 1em;
  .btn {
    font-size: 23px;
  }
}
