
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      




























































































































































.replenishment-form {
  position: relative;
  text-align: center;
  margin-bottom: 2em;
  box-shadow: 0 0 60px rgba($color-primary, 0.6);
  &::before {
    content: "";
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: white;
    transition: opacity 0.3s;
  }
  &.disabled {
    &::before {
      z-index: 1;
      opacity: 0.6;
    }
  }
  .form-block-submit {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .btn-replenishment {
    margin-left: 1em;
  }
  .input-controller {
    @include respond-to(small, max) {
      font-size: 24px;
      input {
        width: 120px;
      }
    }
  }
  .input-controller-number {
    &:nth-child(1),
    &:nth-child(9) {
      @include respond-to(large, max) {
        display: none;
      }
    }
    &:nth-child(2),
    &:nth-child(8) {
      @include respond-to(x-small, max) {
        display: none;
      }
    }
  }
}
.replenishment-submit {
  text-align: center;
  box-shadow: 0 0 60px rgba($color-primary, 0.6);
  .form-block-submit {
    text-align: center;
    margin-top: 1em;
  }
}
.replenishment-submit-title {
  font-size: 29px;
}
