
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      



































































































.promotion-container {
  position: relative;
}
.promotion-content {
  position: relative;
  z-index: 1;
}
.promotion-list {
  width: 100%;
  position: absolute;
  top: 3.4em;
  padding-bottom: 2em;
  font-size: $font-size-xxx-large;
  transform: translateZ(0);
  &.blur {
    .promotion-list-item {
      @include respond-to(medium, max) {
        margin-bottom: 0;
      }
      &::before {
        opacity: 0.3;
        transform: scale(1.4);
        @include respond-to(medium) {
          opacity: 0.6;
          transform: scale(1);
        }
      }
    }
    .promotion-list-link {
      pointer-events: none;
      opacity: 0;
      transform: scale(0.7);
    }
  }
  @include respond-to(medium) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .promotion-list-item {
      width: 48%;
      width: calc(50% - 0.5em);
      &:nth-last-child(2) {
        margin-bottom: 0;
      }
    }
    .img-container {
      flex-shrink: 0;
    }
  }
}
.promotion-list-item {
  will-change: transform;
  margin-bottom: 1em;
  transition: all 0.6s ease-in-out;
  &:last-child {
    margin-bottom: 0;
  }
}
.promotion-list-link {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  color: white;
  padding: 1em 19%;
  height: 200px;
  background-image: linear-gradient(38deg, #c42037 0%, $color-primary 100%);
  box-shadow: 0 0 0.6em rgba(0, 0, 0, 0.3);
  transition: all 0.4s ease-in-out;

  .img-container {
    margin-right: 1em;
    flex-shrink: 0;
  }

  &:hover {
    opacity: 0.6;
  }
}
.promotion-list-item-title {
  font-size: 20px;
  font-weight: 700;
  @include respond-to(x-small) {
    font-size: 27px;
  }
}
.promotion-list-item {
  position: relative;
  will-change: box-shadow;
  &::before {
    content: "";
    position: absolute;
    left: 10%;
    top: 50%;
    display: block;
    width: 80%;
    box-shadow: 0 0 13em 3em $color-primary;
    opacity: 0;
    transition: all 1s ease-in-out;
    transform: scale(0.6)
  }
  &:nth-child(2) {
    &::before {
      box-shadow: 0 0 13em 3em #9c04ef;
    }
  }
  &:nth-child(3) {
    &::before {
      box-shadow: 0 0 13em 3em #fbd21e;
    }
  }
  &:nth-child(4) {
    &::before {
      box-shadow: 0 0 13em 3em $color-primary;
    }
  }
  &:nth-child(5) {
    &::before {
      box-shadow: 0 0 13em 3em #624fd9;
    }
  }
  &:nth-child(6) {
    &::before {
      box-shadow: 0 0 13em 3em $color-primary;
    }
  }

}
