
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      












































































































.wallet-top {
  background-color: $color-primary;
  background-image: linear-gradient(36deg, #c42037 0%, $color-primary 100%);
  color: white;
  border-radius: 10px;
  margin-bottom: 2em;
  padding: 2em;
  box-shadow: 0 0 60px rgba($color-primary, 0.6);

  @include respond-to(small) {
    padding: 3em 4em;
  }
}
.wallet-top-layout {
  text-align: center;
  @include respond-to(small) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;

    .wallet-value {
      margin-bottom: 0;
    }
  }
}
.wallet-title {
  font-size: 33px;
}
.wallet-value {
  font-size: 33px;
  font-weight: 300;
  margin-bottom: 0.2em;

  @include respond-to(x-small) {
    font-size: 51px;
  }
}
.wallet-link {
  display: inline-block;
  box-shadow: 0 8px 46px rgba(0, 0, 0, 0.46);
  background-color: white;
  color: $color-primary;
  border-radius: 10px;
  padding: 0.6em 1em;
  font-size: 26px;

  &:hover {
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.46);
  }
}
.wallet-bottom {
  box-shadow: 0 16px 60px rgba(0, 0, 0, 0.22);
  border-radius: 10px;
  background-color: white;
  color: $color-text;
  padding: 3em 0;
}
.wallet-bottom-title {
  padding-left: 4rem;
  font-size: 33px;
  margin-bottom: 1em;
}
.operation-none-message {
  padding: 0 4rem;
}
