
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      
































































































































































.terms-of-use-page {
  padding: 2em 0;
}
.terms-of-use-top {
  text-align: right;
  font-weight: bold;
  margin-bottom: 1em;
}
h1 {
  text-align: center;
  font-size: 24px;
  margin-bottom: 1em;
}
ol {
  counter-reset: section;
  list-style-type: none;
  & > li::before {
    counter-increment: section;
    content: counters(section,".") ". ";
  }
}
h2 {
  display: inline;
}
.main-list {
  & > li:not(:last-child) {
    margin-bottom: 1em;
  }
}
