
        @import "@/assets/scss/utils/_variables.scss";
        @import "@/assets/scss/utils/_mixins.scss";
      



























.upme-toggle-menu {
  cursor: pointer;
  width: 26px;
  &::before,
  &::after {
    content: "";
  }
  &::before,
  &::after,
  span {
    display: block;
    width: 100%;
    height: 3px;
    background-color: #f80612;
  }
  &::before,
  span {
    margin-bottom: 4px;
  }
  @include respond-to(medium) {
    display: none;
  }
}
